<template>
  <div class="container">
    <div class="header">
      <div class="title">编辑设备类型</div>
      <div class="back" @click="hanleHide">
        <fa-icon icon-name="undo" />
        返回
      </div>
    </div>
    <el-divider />
    <div class="wrap">
      <el-row>
        <el-col :lg="16" :md="20" :sm="24" :xs="24">
          <el-form ref="editForm" v-loading="loading" :model="form" status-icon label-width="100px" @submit.native.prevent>
            <el-form-item label="类别名称" prop="name">
              <el-input v-model="form.name" size="medium" placeholder="请填写类别名称"/>
            </el-form-item>
            <el-form-item label="规格描述" prop="specs">
              <div v-for="item in form.specs" :key="item.key">
                <el-input v-model="item.key" size="medium" disabled style="width: 200px"/>
                :
                <el-input v-model="item.value" size="medium" style="width: 200px" />
              </div>
              <el-button type="primary" icon="el-icon-plus" size="mini" circle @click="addSpec" />
              <el-button type="danger" icon="el-icon-minus" size="mini" circle @click="deleteSpec"/>
            </el-form-item>
            <el-form-item label="总容量" prop="total_capacity">
              <el-input v-model="form.total_capacity" size="medium" placeholder="请填写总容量(单位：A)"/>
            </el-form-item>
            <el-form-item label="电流阈值" prop="elec_limit">
              <el-input v-model="form.elec_limit" size="medium" placeholder="请填写电流阈值"/>
            </el-form-item>
            <el-form-item label="保证年限" prop="warranty_year">
              <el-input v-model="form.warranty_year" size="medium" placeholder="请填写类别名(单位：年)"/>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submitForm('editForm')">保 存</el-button>
              <el-button @click="resetForm('editForm')">重 置</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import CategoryModel from '@/models/category'
export default {
  name: 'CategoryEdit',
  components: {},
  props: {
    editID: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      loading: false,
      form: {
        name: '',
        specs: [
          { key: '线路1', value: '' },
          { key: '线路2', value: '' }
        ],
        elec_limit: '',
        total_capacity: '',
        warranty_year: ''
      },
    }
  },
  computed: {},
  created() { },
  mounted() {
    this.getCategory()
  },
  methods: {
    addSpec() {
      this.form.specs.push({
        key: `线路${this.form.specs.length + 1}`,
        value: ''
      })
    },
    deleteSpec() {
      if (this.form.specs.length > 1) {
        this.form.specs.pop()
      } else {
        this.$message.warning('请至少填写一个设备规格！')
      }
    },
    async getCategory() {
      this.loading = true
      this.form = await CategoryModel.getOne(this.editID)
      this.loading = false
    },
    hanleHide() {
      this.$emit('hanleHide', false)
    },
    async submitForm() {
      let valid = true
      this.form.specs.forEach(item => {
        if (item.value) {
          item.value = parseInt(item.value, 10)
        } else {
          valid = false
        }
      })
      this.form.total_capacity = parseInt(this.form.total_capacity, 10)
      this.form.elec_limit = parseInt(this.form.elec_limit, 10)
      this.form.warranty_year = parseInt(this.form.warranty_year, 10)
      if (!valid) {
        this.$message.warning('规格描述不能为空!')
      } else {
        await CategoryModel.editOne(this.editID, this.form)
        this.$message.success('修改设备类型成功')
        this.hanleHide()
      }
    },
    // 重置表单
    resetForm(formName) {
      this.getCategory()
    },
  },
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .el-divider--horizontal {
    margin: 0;
  }
  .container {
    height: 100%
    .header {
      display flex
      justify-content space-between
      align-items center
      .title {
        height 59px
        line-height 59px
        font-size 16px
        font-weight 500
      }
      .back {
        margin-right 40px
        cursor pointer
      }
    }
    .wrap {
      padding: 20px;
    }
  }
</style>
