/**
 * 公司模块
 */

import {
  post,
  get,
  put,
  _delete
} from '@/utils/request'

export default class Category {
  /**
   *
   * @param {*} size 每页条(默认每页10条数据)
   * @param {*} page 第几页(默认获取第1页的数据；对于数据库而言是第0页)
   */
  static async getList(size, page) {
    const data = await get(`cms/device_category/list?page=${page}&size=${size}`)
    return data
  }

  // 新增设备类型
  static async createOne(name) {
    const data = await post('cms/device_category', name)
    return data
  }

  // 获取企业信息
  static async getOne(id) {
    const data = await get(`cms/device_category/${id}`)
    return data
  }

  // 修改企业信息
  static async editOne(id, name) {
    const data = await put(`cms/device_category/${id}`, name)
    return data
  }

  // 删除s设备类型
  static async deleteOne(id) {
    const data = await _delete(`cms/device_category/${id}`)
    return data
  }
}
